@media (min-width: 768px) {
  .menu__body_left {
    margin: 0px 0px 0px 30px;
  }

  .menu__mobile {
    display: none;
  }

  .menu__list {
    display: flex;
  }

  .menu__list li {
    margin: 0px 38px 0px 0px;
    line-height: 22px;
  }

  .menu__list li:last-child {
    margin: 0px 0px 0px 0px;
  }
}

@media (min-width: 992px) {
  .btn:hover {
    background-color: #e2681c;
  }

  .select .select-options__value:hover {
    background-color: #dadada;
  }

  .checkbox a:hover {
    text-decoration: none;
  }

  .quantity__btn.up:hover {
    background: url("../../dist/img/icons/sl-arrow-o.svg") 0 0/100% no-repeat;
  }

  .quantity__btn.dwn:hover {
    background: url("../../dist/img/icons/sl-arrow-o.svg") 0 0/100% no-repeat;
  }

  .menu__link_btn:hover {
    background-color: #156ce6;
  }

  .menu__link:not(.menu__link_btn):hover {
    opacity: 0.4;
    color: #1b1c1e;
  }

  .content-header__btn:hover {
    transform: scale(1.03);
  }
}

@media (max-width: 992px) {
  .menu__body_right .menu__list li {
    margin: 0px 14px 0px 0px;
  }

  .menu__list li {
    margin: 0px 22px 0px 0px;
  }

  .menu__link_btn {
    padding: 9px 13px;
  }
}

@media (max-width: 768px) {
  body.lock {
    width: 100%;
    position: fixed;
    overflow: hidden;
  }

  body.lock {
    width: 100%;
    position: fixed;
    overflow: hidden;
  }

  .form__info {
    width: 50px;
    height: 50px;
    line-height: 50px;
  }

  .header__menu {
    padding: 35px 0px 0px 0px;
  }

  .menu__body_right {
    display: none;
  }

  .menu__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
    transition: all .5s ease 0s;
    width: 50%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-100%, 0);
  }

  .menu__list li {
    margin: 0px 0px 30px 0px;
  }

  .menu__link {
    font-size: 20px;
  }

  .menu__logo {
    margin: 0px 0px 0px 0px;
    text-align: center;
    width: 100%;
  }

  .icon-menu {
    display: block;
    position: absolute;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 50;
  }

  .icon-menu span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0px;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #000;
  }

  .icon-menu span:first-child {
    top: 0px;
  }

  .icon-menu span:last-child {
    top: auto;
    bottom: 0px;
  }

  .icon-menu.active span {
    transform: scale(0);
  }

  .icon-menu.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }

  .icon-menu.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }

  .content-header__title {
    font-size: 48px;
  }

  .container {
    padding: 0px 10px;
  }
}

@media (max-width: 480px) {
  .header .header__wrapper {
    background-position: 50% 100px;
  }

  .header__menu {
    margin: 0px 0px 50px 0px;
  }

  .header__content {
    padding: 13vh 0px 0px 0px;
  }

  .content-header__title {
    font-size: 38px;
  }

  .content-header__text {
    font-size: 16px;
  }

  .content-header__image {
    display: none;
  }
}